<template>
  <div>
    <Layout>
      <Header :style="{position: 'fixed', width: '100%'}">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-navigate"></Icon>
              用户管理
            </MenuItem>
          </div>
        </Menu>
        <a href="javascript:(0)" @click="layout()"
           style="position: absolute;top: 0;right: 20px;z-index: 1000;color: white;">退出</a>
      </Header>

      <Content :style="{margin: '88px 20px 0', background: '#fff', minHeight: '500px', paddingTop: '20px'}">
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="200">
          <FormItem label="用户名" prop="username" v-if="userId === 0">
            <Input v-model="formValidate.username" placeholder="请输入用户名"></Input>
          </FormItem>
          <FormItem label="昵称" prop="nickname">
            <Input v-model="formValidate.nickname" placeholder="请输入昵称"></Input>
          </FormItem>
          <FormItem label="密码" prop="password">
            <Input v-model="formValidate.password" placeholder="请输入密码"></Input>
          </FormItem>
          <FormItem label="学号" prop="no" v-if="userId === 0">
            <Input v-model="formValidate.no" placeholder="请输入学号"></Input>
          </FormItem>
          <FormItem label="年龄" prop="age">
            <Input v-model="formValidate.age" placeholder="请输入年龄"></Input>
          </FormItem>
          <FormItem label="电话" prop="phone">
            <Input v-model="formValidate.phone" placeholder="请输入电话"></Input>
          </FormItem>
          <FormItem label="性别" prop="gender">
            <RadioGroup v-model="formValidate.gender">
              <Radio :label="1" value="1">男</Radio>
              <Radio :label="2" value="2">女</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="角色" prop="roleId">
            <RadioGroup v-model="formValidate.roleId">
              <Radio :label="2" :value="2">教师</Radio>
              <Radio :label="3" :value="3">学生</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="submit('formValidate')">提交</Button>
            <Button @click="back()" style="margin-left: 8px">返回</Button>
          </FormItem>
        </Form>
      </Content>
    </Layout>
  </div>
</template>

<script>
  import {fetchGet, fetchPost} from "../../api";

  export default {
    name: "User",
    data() {
      const validatePass = (rule, value, callback) => {
        if (this.userId === 0 && value === '') {
          callback(new Error('密码不能为空'));
        } else {
          callback()
        }
      }

      const validateAge = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        // 模拟异步验证效果
        setTimeout(() => {
          if (!value) {
            callback(new Error('年龄不能为空'));
          } else {
            let reg = new RegExp(/^[0-9]*$/)
            if (!reg.test(Number(value))) {
              callback(new Error('年龄只能为数字'));
            } else {
              callback();
            }
          }
        }, 1000);
      }

      return {
        userId: 0,
        formValidate: {
          username: '',
          password: '',
          nickname: '',
          no: '',
          age: '',
          phone: '',
          gender: 0,
          roleId: 0
        },
        ruleValidate: {
          username: [
            {required: true, message: '用户名不能为空!', trigger: 'blur'}
          ],
          password:[
            {validator: validatePass, trigger: 'blur'}
          ],
          nickname: [
            {required: true, message: '昵称不能为空!', trigger: 'blur'},
          ],
          no: [
            {required: true, message: '学号不能为空!', trigger: 'blur'},
            {pattern: /^\d{6,}$/, message: "学号为不少于6位的数字", trigger: "blur"}
          ],
          age: [
            {validator: validateAge, trigger: 'blur'}
            // {required: true, message: '年龄不能为空!', trigger: 'blur'},
            // {pattern: /^[0-9]*$/, message: "年龄只能为数字", trigger: "blur"}
          ],
          phone: [
            {required: true, message: '手机号不能为空', trigger: 'blur'},
            {pattern: /^1[3456789]\d{9}$/, message: "手机号码格式不正确", trigger: "blur"}
          ],
        }
      }
    },
    mounted() {
      this.userId = this.$route.query.id ? this.$route.query.id : 0
      if (this.userId != null) {
        this.queryUserInfo(this.userId)
      }
    },
    methods: {
      queryUserInfo(data) {
        let obj = {
          userId: data
        }
        let formData = new FormData();
        formData.append('userId', data)
        fetchGet('/v1/user/getUser', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            this.formValidate = data
            if (this.userId !== 0) {
              this.formValidate.password = null
              // this.formValidate.age = Number(this.formValidate.age.toString())
            }
            this.formValidate.roleId = data.role.id
          }
        })
      },
      submit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = {}
            obj = this.formValidate
            fetchPost('/v1/user/saveUser', obj).then(res => {
              if (res.code === 200) {
                this.$Message.info("添加用户成功!");
                this.$router.push({path: '/admin/users'})
              } else {
                this.$Message.error(res.message);
              }
            })
          } else {
            this.$Message.error('请按照提示修改表单');
          }
        })
        if (this.userId === 0 && this.formValidate.password === '') {
          this.$Message.info('新建用户密码不能为空!');
          return
        }

      },
      back() {
        this.$router.push({path: '/admin/users'})
      },
      layout() {
        localStorage.removeItem("token")
        localStorage.removeItem("userInfo")
        this.$router.push({path: '/'})
      }
    }
  }
</script>

<style scoped>
  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
</style>



